<template>
  <div class="back-white" v-if="(getActiveCategory == getCategories[0] || getActiveCategory == getCategories[category])">
    <div class="df align-center menu-item space-between" @click="prodAdd(id)">
      <div class="df align-center">
        <div class="prod-img">
          <img :src="require(`@/assets/${img}`)" />
        </div>
        <div class="prod-content">
          <h3>{{title}}</h3>
          <p v-if="description !== null">{{description}}</p>
        </div>
      </div>
      
      <div class="df align-center">
          <div class="prod-prices">
            <p v-if="price_old[getCurrency] !== null" class="old">
              <s>{{price_old[getCurrency]}} {{getCurrency}}</s>
            </p>
            <p class="new">{{price[getCurrency]}} {{getCurrency}}</p>
          </div>
          <div v-if="promo !== null" class="prod-promo">
            <p>{{promo}}</p>
          </div>
          <div>
            <button @click.native="prodAdd(id)" class="prod-add">+</button>
          </div>
      </div>
    </div>
    <hr />
  </div>
</template>

<script>

export default {
  name: "MenuItem",
  props: {
    id: Number,
    title: String,
    description: String,
    img: String,
    price_old: Object,
    price: Object,
    promo: String,
    category: Number
  },
  data: function() {
    return {
      repeat: 0,
      menuItems: this.$store.getters.getMenuItems,
      lastOrder: this.$store.getters.getOrders[
        this.$store.getters.getOrders.length - 1
      ]
    };
  },
  computed: {
    getCurrency() {
      return this.$store.getters.getCurrency;
    },
    getActiveCategory() {
      return this.$store.getters.getActiveCategory;
    },
    getCategories(){
      return this.$store.getters.getCategories;
    }
  },
  methods: {
    prodAdd: function(id) {
      this.$store.dispatch("addOrderAction", this.menuItems[id - 1]);
      var lastOrder = this.$store.getters.getOrders[this.$store.getters.getOrders.length - 1];
      this.$store.dispatch("changeSumAction", {
        price: Number(lastOrder.price[this.$store.getters.getCurrency]),
        action: "add"
      });
    }
  }
};
</script>

