<template>
  <ul class="box-shadow back-white">
    <li
      @click="active($event, index)"
      :class="(index==0 ? 'active' : '') +' menu_list_item'"
      v-for="(menu, index) in getCategories"
      :key="index"
    >{{menu}}</li>
  </ul>
</template>

<script>
export default {
  name: "MenuList",
  data: function() {
    return {
    };
  },
  computed:{
    getCategories(){
      return this.$store.getters.getCategories;
    }
  },
  methods: {
    active: function(ev, index) {
      let list = document.querySelectorAll(".menu_list_item");
      for (let item of list) {
        if(item.classList.contains("active")){
            item.classList.remove("active");
        }
      }
      ev.target.classList.add("active");
      this.$store.dispatch('setActiveCategoryAction', this.$store.getters.getCategories[index])
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
ul li {
  list-style: none;
  font-weight: bold;
  padding: 8px;
  cursor: pointer;
}
.active {
  background-color: #81c02e;
  color: white;
}
ul li:hover {
  background-color: #81c02e;
  color: white;
}
</style>
