import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

import menuItems from "../data/menuItems.json";

const state = {
    orders: [],
    cartOrders: [],
    currency: 'zł',
    menu_list: [
        "Wszystko",
        "Zestawy",
        "Burgery",
        "Sałatki",
        "Desery",
        "Napoje"
    ],
    active_category: "Wszystko",
    sum: 0
};

const mutations = {
    addOrder(state, order) {
        if (!state.orders.includes(order)) {
            state.cartOrders.push({
                'id': order.id,
                'count': 1,
                'title': order.title,
                'description': order.description,
                'short_description': order.short_description,
                'price': order.price
            });
        } else {
            for (let co of state.cartOrders) {
                if (co['id'] == order.id) {
                    co['count']++;
                }
            }
        }
        state.orders.push(order);
    },
    removeOrder(state, order) {
        for (let co of state.cartOrders) {
            if (co['id'] == order.id) {
                co['count']--;
                if(co['count'] <= 0){
                    
                    var indexCartOrders = state.cartOrders.indexOf(co);
                    if (indexCartOrders > -1) {
                        state.cartOrders.splice(indexCartOrders, 1);
                    }
                }
                
            }
        }
        for (let o of state.orders) {
            if (o['id'] == order.id) {
                var indexOrders = state.orders.indexOf(o);
                if (indexOrders > -1) {
                    state.orders.splice(indexOrders, 1);
                }
                return;
            }
        }
    },
    setActiveCategory(state, category) {
        state.active_category = category;
    },
    changeSum(state, param) {
        if (param.action == "add") {
            state.sum = Number((state.sum+param.price).toFixed(2));
        } else {
            state.sum = Number((state.sum-param.price).toFixed(2));
        }
    }
};

const actions = {
    addOrderAction(state, order) {
        state.commit('addOrder', order)
    },
    removeOrderAction(state, order) {
        state.commit('removeOrder', order)
    },
    setActiveCategoryAction(state, category) {
        state.commit('setActiveCategory', category)
    },
    changeSumAction(state, param) {
        state.commit('changeSum', param)
    }
};

const getters = {
    getOrders(state) {
        return state.orders;
    },
    getCartOrders(state) {
        return state.cartOrders;
    },
    getCurrency(state) {
        return state.currency;
    },
    getActiveCategory(state) {
        return state.active_category;
    },
    getCategories(state) {
        return state.menu_list;
    },
    getMenuItems() {
        return menuItems;
    },
    getSum(state) {
        return state.sum;
    }
};

export default new Vuex.Store({
    state,
    mutations,
    actions,
    getters
});