<template>
  <div class="box-shadow back-white">
    <h3>Twoje zamówienie:</h3>
    <hr />
    <p class="pad-05" v-if="getOrders.length <= 0">Zamów swoje ulubione danie!</p>
    <div v-for="(order, index) in getCartOrders" :key="index">
      <div class="df align-center pad-05 space-between">
        <div class="df align-center">
          <div class="cart-count">
            <button @click="prodAdd(order.id)">+</button>
            <p class="text_center">x {{order.count}}</p>
            <button @click="prodRemove(order.id)">-</button>
          </div>

          <div class="cart-content">
            <h4 class="cart-title">{{order.title}}</h4>
            <p v-if=" order.short_description !== '' " class="cart-desc">{{order.short_description}}</p>
            <p v-else-if=" order.description !== null " class="cart-desc">{{order.description}}</p>
          </div>
        </div>
        <div class="df align-center cart-order-price">
          <p>
            <strong>{{order.price[getCurrency]}} {{getCurrency}}</strong>
          </p>
        </div>
      </div>
    </div>
    <hr />
    <div class="df space-between sum pad-05">
      <p>Suma:</p>
      <p>{{getSum}} {{getCurrency}}</p>
    </div>
    <div class="pad-05">
      <small
        v-if="getSum < minPriceOrder"
      >Minimalna kwota zamówienia nie została osiągnięta ({{minPriceOrder}} {{getCurrency}})</small>
    </div>

    <button class="order-btn" :disabled="getSum < minPriceOrder">ZAMAWIAM</button>
  </div>
</template>

<script>
export default {
  name: "Cart",
  data: function() {
    return {
      minPriceOrder: 20,
      menuItems: this.$store.getters.getMenuItems
    };
  },
  computed: {
    getOrders() {
      return this.$store.getters.getOrders;
    },
    getCartOrders() {
      return this.$store.getters.getCartOrders;
    },
    getCurrency() {
      return this.$store.getters.getCurrency;
    },
    getSum() {
      return this.$store.getters.getSum;
    }
  },
  methods: {
    prodAdd: function(id) {
      this.$store.dispatch("changeSumAction", {
        price: Number(
          this.menuItems[id - 1].price[this.$store.getters.getCurrency]
        ),
        action: "add"
      });
      this.$store.dispatch("addOrderAction", this.menuItems[id - 1]);
    },
    prodRemove: function(id) {
      this.$store.dispatch("changeSumAction", {
        price: Number(
          this.menuItems[id - 1].price[this.$store.getters.getCurrency]
        ),
        action: "remove"
      });
      this.$store.dispatch("removeOrderAction", this.menuItems[id - 1]);
    }
  }
};
</script>

